import React from 'react'
import './Hire.css'
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import InstagramIcon from '@material-ui/icons/Instagram';
import FacebookIcon from '@material-ui/icons/Facebook';
import GitHubIcon from '@material-ui/icons/GitHub';
import FiverrIcon from '../images/fiverr.png'
import FormControl from '@material-ui/core/FormControl'
import InputLabel from '@material-ui/core/Input'
import Input from '@material-ui/core/Input'
import FormHelperText from '@material-ui/core/FormHelperText'
import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'
import emailjs from 'emailjs-com'
import EmailIcon from '@material-ui/icons/Email';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import video from '../images/backgroundVideo.mp4'
import MuiAlert from '@material-ui/lab/Alert';
import Snackbar from '@material-ui/core/Snackbar';
function Hire() {
  function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }

    const [values, setValues] = React.useState({
        email: '',
        subject: '',
        message: ''
  });
  const handleChange =  (e) => {
        setValues({ ...values, [e.target.id]: e.target.value });
  };

  const [open, setOpen] = React.useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));


  const [open2, setOpen2] = React.useState(false);

  const handleClick = () => {
    setOpen2(true);
  };

  const handleClose2 = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen2(false);
  };
  const handleClose = () => {
    setOpen(false);
  };

  const reset = () => {
    setValues({email: "", subject: "", message: ""})
}
const formValidate = (e) => {
  if(values.email.includes("@") &&  values.email.includes(".com") ){ if(values.subject.length > 2){ if(values.message.length > 4){ return  (sendEmail(e)) } else { handleClick() }} else { handleClick() } }
  else { handleClick() }
}

  const sendEmail = (e) => {
    emailjs.init('user_csHlZlyxLBJpueJqtTp2L');

    emailjs.send("service_lrb78yf","template_nrv8xa4",{
        subject: values.subject,
        email: values.email,
        message: values.message
    });
    setOpen(true);
     reset()
  }

  const handleSubmit = (e) => {
      
      // HERE: you always want to prevent default, so do this first
      e.preventDefault();
      formValidate(e);


    };

    return (
        <div className="hero-container2">
        <video  className="video2"  src={video} autoPlay loop muted></video>
            <h1>Hire Me</h1>
            <Typography style={{fontSize:"25px", fontFamily:"'Montserrat', sans-serif"}} gutterBottom variant="h5" component="h2">
          Want to work together ? <br /> <br />
          <EmailIcon id="eicon" />
          
          </Typography>
          
          <Typography style={{fontSize:"18px", fontFamily:"'Montserrat', sans-serif", marginTop:"0", paddingTop:"0"}} variant="body2" color="textSecondary" component="p">
          <br /> <br /><br />
          <FormControl style={{opacity:"200%"}} onSubmit={(e) => handleSubmit(e)}>
          <TextField  id="email" label="Email Address" name="email" value= {values.email}
          type='text' onChange= {(e)=> handleChange(e)} variant="outlined" required  />
          <TextField style={{marginTop:"10px"}} id="subject" name="subject"  value= {values.subject}
          type='text' onChange= {(e)=> handleChange(e)} label="Subject" variant="outlined" required />
          <TextField
          style={{marginTop:"10px", marginBottom:"20px"}}
          id="message"
          label="Message"
          multiline
          rowsMax={4}
          variant="outlined"
          name="message"
          required
          value= {values.message}
          type='text'
          id="message"
          onChange= {(e)=> handleChange(e)}
        />


          <Button style={{backgroundColor:"black"}} variant="contained"  onClick={(e) => handleSubmit(e)} color="primary" disableElevation>
  Submit
</Button>
      <Snackbar open={open2} autoHideDuration={6000} onClose={handleClose2}>
        <Alert onClose={handleClose2} severity="error">
          Error!
        </Alert>
      </Snackbar>
</FormControl>
<Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title"> <h1 style={{margin:"0"}} > Great Choice! </h1> </DialogTitle>
        <DialogContent>
          <DialogContentText style={{marginTop:"-150px", fontSize:"18px", fontFamily:"'Montserrat', sans-serif"}} >
            Received your details! Will get back to you super soon.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleClose} color="primary" autoFocus>
            Okay
          </Button>
        </DialogActions>
      </Dialog>
            </Typography>

            <div style={{display:"flex", flexDirection:"row"}} >
             <Link href="https://www.fiverr.com/puneetkathar">
             <img style={{height: '55px', marginTop:"75px", color:"black"}} src={FiverrIcon} />
            </Link>
            </div>
            
           <div style={{display:"flex", flexDirection:"row"}} > <Link href="https://www.linkedin.com/in/puneetkathar1/">
            <LinkedInIcon style={{fontSize:"30px", marginRight:"35px", marginTop:"5px", color:"black"}} />
            </Link>
            <Link href="https://www.instagram.com/puneetkathar1">
            <InstagramIcon style={{fontSize:"30px", marginRight:"35px", marginTop:"5px", color:"black"}} />
            </Link>
            <Link href="https://www.facebook.com/profile.php?id=100006108909411">
            <FacebookIcon style={{fontSize:"30px", marginRight:"35px", marginTop:"5px", color:"black"}} />
            </Link>
            <Link href="https://github.com/puneetkathar1">
            <GitHubIcon style={{fontSize:"30px", marginRight:"35px", marginTop:"5px", color:"black"}} />
            </Link></div>
        </div>
    )
}

export default Hire
